import { h } from 'preact';
import { useEffect, useState } from 'preact/hooks';
import { route } from 'preact-router';

import Error_MSG from '../../components/Error_MSG';
import style from './style.css';


const Menu = () => {
  const [duplicateLoading, setDuplicateLoading] = useState(false);
  const [exportLoading, setExportLoading] = useState(false);
  const [menuError, setMenuError] = useState({
      error: false,
      msg: ""
  });

  useEffect(() => {
    setMenuError({ error: false, msg: "" });

    window.chrome.tabs.onUpdated.addListener((tabId, changeInfo) => {
      if(changeInfo.url != null){
        //console.log(`onUpdated. url is ${changeInfo.url}`);

        const regex = /admin\/menus\/[0-9]+(\?.*)?$/;
        const regex1 = /store\/.*\/menus\/[0-9]+(\?.*)?$/;
        const regex2 = /admin\/menus\/?(\?.*)?$/;
        const regex3 = /store\/.*\/menus\/?(\?.*)?$/;

        if (regex.test(changeInfo.url) || regex1.test(changeInfo.url)) {
          route('/menu');
          return;
        } else if (regex2.test(changeInfo.url) || regex3.test(changeInfo.url)) {
          route('/menus');
          return;
        }
      }
    });
  }, []);

  const duplicateHandler = () => {
    setDuplicateLoading(true);

    //console.log('%c[DUPLICATING....]', 'color:black; background:yellow;');

    return executeScript()
    .then(getActiveTab)
    .then((tab) => {
      window.chrome.tabs.sendMessage(
        tab.id,
        { type: 'getHostData' },
        async (response) => {
          try {
            const { url } = response.result;
            //console.log(response);
            let regex = /https?:\/\/(.*\.myshopify.com)\/.*\/menus\/([0-9]+)\/?/;

            if (url.includes("admin.shopify.com")) {
              regex = /https?:\/\/(admin.shopify.com)\/store\/.*\/menus\/([0-9]+)\/?/;
            }

            const [, origin] = regex.exec(url);
            const cookies = await window.chrome.cookies.getAll({ domain: origin });
            const cookie_prepared = cookies.map(i => `${i.name}=${i.value}`).join('; ');

            const results = await fetch('https://meraki-apps.com/duplicate', {
              method: 'POST',
              mode: 'cors',
              headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({ ...response.result, cookie: cookie_prepared }),
            }).then(resp => resp.json());

            if (results.error || !results.success) {
              throw Error('Operation Failed!');
            }

            window.chrome.tabs.sendMessage(
              tab.id,
              {
                type: 'openNewMenu',
                data: results.data
              }
            );

            setDuplicateLoading(false);
          } catch (error) {
            console.log("1. Caught Error", error);
            setDuplicateLoading(false);
            setErrorMSG();
          }
        }
      );
    })
    .catch(error => {
      console.log("2. Caught Error", error);
      setDuplicateLoading(false);
      setErrorMSG();
    });
  };

  const exportHandler = async () => {
    setExportLoading(true);

    //console.log('%c[EXPORTING....]', 'color:black; background:yellow;');

    return executeScript()
    .then(getActiveTab)
    .then((tab) => {
      window.chrome.tabs.sendMessage(
        tab.id,
        { type: 'getHostData' },
        async (response) => {
          try {
            const { csrfToken, appId, url } = response.result;
            //console.log(response);
            let regex = /https?:\/\/(.*\.myshopify.com)\/.*\/menus\/([0-9]+)\/?/;

            if (url.includes("admin.shopify.com")) {
              regex = /https?:\/\/(admin.shopify.com)\/store\/.*\/menus\/([0-9]+)\/?/;
            }

            const [, origin] = regex.exec(url);
            const cookies = await window.chrome.cookies.getAll({ domain: origin });
            const cookie_prepared = cookies.map(i => `${i.name}=${i.value}`).join('; ');

            const results = await fetch('https://meraki-apps.com/api/2024-04/export', {
              method: 'POST',
              mode: 'cors',
              headers: {
                'content-type': 'application/json'
              },
              body: JSON.stringify({ csrfToken, appId, url, cookie: cookie_prepared }),
            }).then(resp => resp.json());

            if (results.error || !results.success) {
              throw Error('Operation Failed!');
            }

            const menu = results.data;
            const menuHandle = menu.handle || 'menu_data';

            const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(menu));
            const dlAnchorElem = document.createElement('a');
            dlAnchorElem.setAttribute("href", dataStr);
            dlAnchorElem.setAttribute("download", `${menuHandle}.json`);
            dlAnchorElem.click();
            setExportLoading(false);
          } catch (error) {
            console.log("1. Caught Error", error);
            setExportLoading(false);
            setErrorMSG();
          }
        }
      );
    })
    .catch(error => {
      console.log("2. Caught Error", error);
      setExportLoading(false);
      setErrorMSG();
    });
  };

  const setErrorMSG = () => {
    setMenuError({ error: true, msg: "Operation failed! please refresh page and try again." });
    setTimeout(() => {
      setMenuError({ error: false, msg: '' });
    }, 20000);
  }

  const getActiveTab = async () => {
    const queryOptions = { active: true, currentWindow: true };
    const [tab] = await window.chrome.tabs.query(queryOptions);

    return tab;
  };

  const executeScript = async () => {
    const queryOptions = { active: true, currentWindow: true };
    const [tab] = await window.chrome.tabs.query(queryOptions);

    window.chrome.scripting.executeScript({
      target: { tabId: tab.id },
      files: ['contentScript.js']
    });
  };

	return (
		<div class={style.body}>
      <h2>Shopify Navigation Tool</h2>
      <h5>An absolute time saver</h5>

      <h4 class={style.bottom}>Actions</h4>
      <div class={`${style.actions} ${duplicateLoading || exportLoading ? style.disabled : ""}`}>
        <button
          class={`${duplicateLoading ? style.loading : ""}`}
          onClick={duplicateHandler}
        >
          <svg
            height="16px"
            width="16px"
            fill="#000000"
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            x="0px"
            y="0px"
            viewBox="0 0 65 65"
            enable-background="new 0 0 65 65"
          >
            <title>Duplicate</title>
            <g>
              <path d="M61.3,4.9H46.1V2.7c0-1.6-1.3-2.9-2.9-2.9H4.7c-2.2,0-4,1.8-4,4v53.4c0,1.6,1.3,2.9,2.9,2.9h15.3v2.3   c0,1.6,1.3,2.9,2.9,2.9h29.9l12.5-12.5V7.8C64.3,6.2,62.9,4.9,61.3,4.9z M20.4,62.3V48.5h-1.5v10H3.7c-0.8,0-1.4-0.6-1.4-1.4V3.7   c0-1.4,1.1-2.5,2.5-2.5h38.4c0.8,0,1.4,0.6,1.4,1.4v2.3H22.9c-2.2,0-4,1.8-4,4V44h1.5V8.9c0-1.4,1.1-2.5,2.5-2.5h38.4   c0.8,0,1.4,0.6,1.4,1.4v43.8h-8.1c-2.2,0-4,1.8-4,4v8.1H21.8C21.1,63.8,20.4,63.1,20.4,62.3z M61.7,53.2l-9.5,9.5v-7   c0-1.4,1.1-2.5,2.5-2.5H61.7z" />
              <rect x="25.7" y="27.7" width="25.8" height="1.5" />
              <rect x="25.7" y="34.8" width="20.9" height="1.5" />
              <rect x="25.7" y="14.1" width="31.8" height="1.5" />
              <rect x="25.7" y="20.9" width="31.8" height="1.5" />
              <path d="M29.5,42.3l3.3,3.3h-13c-5.1,0-9.3-4.1-9.3-9.3V25.4H9v10.9c0,5.9,4.8,10.8,10.8,10.8h13l-3.3,3.3l1.1,1.1l5.1-5.1   l-5.1-5.1L29.5,42.3z" />
            </g>
          </svg>
          <span class={style.label}>Duplicate</span>
          <span class={style.ball_pulse}>
            <span />
            <span />
            <span />
          </span>
        </button>
        <button
          class={`${exportLoading ? style.loading : ""}`}
          onClick={exportHandler}
        >
          <svg
            height="16px"
            width="16px"
            fill="#000000"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 64 64"
            x="0px"
            y="0px"
          >
            <title>Export</title>
            <g><path d="M9.255,22.718a1.5,1.5,0,0,1,0-2.121l7.428-7.429a1.5,1.5,0,0,1,.493-.327l.022-.007a1.454,1.454,0,0,1,1.095,0l.022.007a1.486,1.486,0,0,1,.493.327L26.236,20.6a1.5,1.5,0,0,1-2.121,2.121l-4.869-4.87v4.78A14.893,14.893,0,0,0,34.122,37.505H42.78a1.5,1.5,0,0,1,0,3H34.122A17.9,17.9,0,0,1,16.246,22.628v-4.78l-4.87,4.87a1.5,1.5,0,0,1-2.121,0Zm37.43-9.991H31.35a1.5,1.5,0,0,0,0,3H46.685a5.506,5.506,0,0,1,5.5,5.5V42.773a5.506,5.506,0,0,1-5.5,5.5H17.315a5.506,5.506,0,0,1-5.5-5.5V31.16a1.5,1.5,0,0,0-3,0V42.773a8.51,8.51,0,0,0,8.5,8.5h29.37a8.51,8.51,0,0,0,8.5-8.5V21.227A8.51,8.51,0,0,0,46.685,12.727Z" /></g>
          </svg>
          <span class={style.label}>Export</span>
          <span class={style.ball_pulse}>
            <span />
            <span />
            <span />
          </span>
        </button>
      </div>
      <Error_MSG result={menuError} />
		</div>
	);
}

export default Menu;
